import { useSelector, useDispatch } from "react-redux";
import { loginUser, outUser } from "../store/action/userActions";
const useAuth = () => {
    return useSelector((state) => state.userReducer);
};
const useSetAuth = () => {
    const dispatch = useDispatch();
    return (access) => {
        if (typeof access.accessToken === 'string') {
            localStorage.setItem('accessToken', access.accessToken);
            dispatch(loginUser(access));
        }
        else {
            localStorage.removeItem('accessToken');
            dispatch(outUser);
        }
    };
};
export { useAuth, useSetAuth, };
